import React from 'react';
import c from 'classnames';
import { DemandFrontier, EAFIT, Globant, HUGE, Question, Star } from './Icons';


type Position = "left" | "right" | "top" | "bottom";
interface TimelineChipProps {
    position?: Position;
    text: string;
    className?: string;
}

const TimelineChip = ({ position, text, children, className }: React.PropsWithChildren<TimelineChipProps> = {
    position: 'left', text: '', className: ''
}) => {
    return <div
        data-text={text}
        className={
            c([
                "flex items-center order-1 shadow-xl w-8 h-8 rounded-full timeline-chip select-none",
                {
                    'timeline-chip--left': position === 'left',
                    'timeline-chip--right': position === 'right',
                },
                className
            ])
        }>
        {children}
        <p className="sr-only">{text}</p>
    </div>
}

interface TimelineRowProps {
    position?: Position;
    chip: React.ReactNode;
    className?: string;
}

const TimelineRow = ({ position, children, chip, className }: React.PropsWithChildren<TimelineRowProps> = {
    position: 'left',
    chip: null,
    className: ''
}) => {
    return (
        <div className={
            c("timeline-row", { "timeline-row--left": position === 'left' }, className)
        }>
            <div className="order-1 w-5/12" />
            {chip}
            <div className="order-1 w-5/12">
                {children}
            </div>
        </div>
    );
}

