import React, { useContext, useEffect, useLayoutEffect } from 'react';
import { MapaContext, ThemeContext } from '../context';
import { Icons } from '../components';
import { useWindowSize } from 'react-use';
import c from 'classnames';

export default function ThemeSelector(
	{ className, ...props }: React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>
) {
	const { theme, setTheme, source } = useContext(ThemeContext);
	const { mapa } = useContext(MapaContext);

	const { width, height } = useWindowSize();

	useEffect(
		() => {
			if (theme === 'light') {
				document.documentElement.classList.add('light');
				document.documentElement.classList.remove('dark');
			} else {
				document.documentElement.classList.add('dark');
				document.documentElement.classList.remove('light');
			}
		},
		[theme]
	);

	useLayoutEffect(
		() => {
			setTimeout(() => {
				!!mapa && mapa.invalidateSize();
			}, 100);
		},
		[width, height, mapa]
	);

	const button = (<button
		onClick={() => {
			if (source === 'system') {
				setTheme('dark');
			} else if (source === 'dark') {
				setTheme('light');
			} else if (source === 'light') {
				setTheme('system');
			}
		}}
		{...props}
	>
		<span className="sr-only">Change web style to {theme === 'light' ? 'dark' : 'light'} theme</span>
		{source === 'system' && <Icons.SystemBrightness width="26" className="fill-current" />}
		{source !== 'system' && <Icons.Brightness width="26" className="fill-current" />}
	</button>);

	return (
		<span className={c('relative flex', className)} {...props}>
			{button}
			<span className="absolute right-8 top-1 inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none bg-black dark:bg-white rounded-full text-white dark:text-black se:hidden">{source}</span>
		</span>
	);
}
