import { createContext } from 'react';

interface Theme {
	theme: 'dark' | 'light';
	source: 'system' | 'dark' | 'light';
	setTheme: (theme: 'system' | 'dark' | 'light') => void;
}

const ThemeContext = createContext<Theme>({
	theme: 'dark',
	source: 'system',
	setTheme: () => { }
});

export default ThemeContext;
